import React, { useState, useEffect } from "react";
import { MDBInput, MDBBtn, MDBIcon } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import Axios from "axios";

export default function ContactForm() {
  const [name, HandelName] = useState("");
  const [email, HandelEmail] = useState("");
  const [subject, HandelSubject] = useState("");
  const [message, HandelMessage] = useState("");
  const [value, setValue] = useState({});
  const [laoder, setLoader] = useState(false);
  const [success, setSuccess] = useState(false);

  const HandelSubmit = (e) => {
    e.preventDefault();

    setValue((value) => ({
      ...value,
      name,
      email,
      subject,
      message
    }));
  };
  function isEmpty(obj) {
    for (var key in obj) {
      if (obj.hasOwnProperty(key)) return false;
    }
    return true;
  }
  useEffect(() => {
    if (!isEmpty(value)) {
      setLoader(true);
      Axios.post("https://portfoliob.herokuapp.com/api/mailcontact", value)
        .then(() => {
          setLoader(false);
          setSuccess(true);
        })
        .catch((err) => {
          setLoader(false);
        });
    }
  }, [value]);

  return (
    <form className="forms" onSubmit={HandelSubmit}>
      <p className="h5 mb-4">fell free to contact me</p>
      {success ? (
        <span className="success-message">
          <p>
            Thank you for your trust we will contact you in less than 24 hours
          </p>
        <p> <FontAwesomeIcon icon={faCheckCircle} /></p> 
        </span>
      ) : (
        <React.Fragment>
          <div className="grey-text">
            <MDBInput
              label="Your name"
              onChange={(e) => {
                HandelName(e.target.value);
              }}
              icon="user"
              group
              type="text"
              validate
              error="wrong"
              success="right"
            />
            <MDBInput
              label="Your email"
              onChange={(e) => {
                HandelEmail(e.target.value);
              }}
              icon="envelope"
              group
              type="email"
              validate
              error="wrong"
              success="right"
            />
            <MDBInput
              label="Subject"
              onChange={(e) => {
                HandelSubject(e.target.value);
              }}
              icon="tag"
              group
              type="text"
              validate
              error="wrong"
              success="right"
            />
            <MDBInput
              type="textarea"
              onChange={(e) => {
                HandelMessage(e.target.value);
              }}
              rows="2"
              label="Your message"
              icon="pencil-alt"
            />
          </div>
          <div className="text-center">
            <MDBBtn outline color="secondary" type="submit">
              {!laoder ? (
              <span> send <MDBIcon far icon="paper-plane" className="ml-1" /></span>  
              ) : (
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              )}
            </MDBBtn>
          </div>
        </React.Fragment>
      )}
    </form>
  );
}
